import Vue from "vue";
import Router from "vue-router";
import { linkWallet } from "../api/linkedWallet.js";
Vue.use(Router);
const router = new Router({
  routes: [
    {
      path: "/",
      name: "startUp",
      component: () => import("@/view/startUp.vue"),
      meta: {
        title: "启动"
      }
    },
    {
      path: "/index",
      name: "index",
      component: () => import("@/view/index/index.vue"),
      meta: {
        title: "首页"
      }
    },

    {
      path: "/personalCenter",
      name: "personalCenter",
      component: () => import("@/view/personalCenter/index.vue"),
      meta: {
        title: "个人中心"
      }
    },

    {
      path: "/productDetails",
      name: "productDetails",
      component: () => import("@/view/index/productDetails.vue"),
      meta: {
        title: "商品详情"
      }
    },

    {
      path: "/getPoints",
      name: "getPoints",
      component: () => import("@/view/getPoints/index.vue"),
      meta: {
        title: "获取积分"
      }
    },

    {
      path: "/myOrder",
      name: "myOrder",
      component: () => import("@/view/personalCenter/myOrder.vue"),
      meta: {
        title: "我的订单"
      }
    },

    {
      path: "/orderDetails",
      name: "orderDetails",
      component: () => import("@/view/order/orderDetails.vue"),
      meta: {
        title: "订单详情"
      }
    },
    {
      path: "/directRewards",
      name: "directRewards",
      component: () => import("@/view/personalCenter/directRewards.vue"),
      meta: {
        title: "直推奖励记录"
      }
    },
    {
      path: "/teamRewards",
      name: "teamRewards",
      component: () => import("@/view/personalCenter/teamRewards.vue"),
      meta: {
        title: "团队奖励记录"
      }
    },

    {
      path: "/purchaseRecords",
      name: "purchaseRecords",
      component: () => import("@/view/personalCenter/purchaseRecords.vue"),
      meta: {
        title: "购买记录"
      }
    },

    {
      path: "/sellingRecords",
      name: "sellingRecords",
      component: () => import("@/view/personalCenter/sellingRecords.vue"),
      meta: {
        title: "卖出记录"
      }
    },
    {
      path: "/addressManagement",
      name: "addressManagement",
      component: () => import("@/view/addressManagement/index.vue"),
      meta: {
        title: "收货地址管理"
      }
    },

    {
      path: "/merchantCenter",
      name: "merchantCenter",
      component: () => import("@/view/merchantCenter/index.vue"),
      meta: {
        title: "商家中心"
      }
    },

    {
      path: "/publishProducts",
      name: "publishProducts",
      component: () => import("@/view/merchantCenter/publishProducts.vue"),
      meta: {
        title: "发布商品"
      }
    },

    {
      path: "/productManagement",
      name: "productManagement",
      component: () => import("@/view/merchantCenter/productManagement.vue"),
      meta: {
        title: "商品管理"
      }
    },
    {
      path: "/withdrawalRecords",
      name: "withdrawalRecords",
      component: () => import("@/view/merchantCenter/withdrawalRecords.vue"),
      meta: {
        title: "提现记录"
      }
    },

    {
      path: "/merchantOrders",
      name: "merchantOrders",
      component: () => import("@/view/merchantCenter/merchantOrders.vue"),
      meta: {
        title: "商家订单"
      }
    }
  ]
});
import store from "@/store/index.js";

router.beforeEach(async (to, from, next) => {
  if (!store.state.userAddress) {
    await linkWallet();
  }
  next();
});
export default router;



