import axios from "axios";
import qs from "qs";
import { Toast } from "vant";
import url from "./url";
const api = axios.create({
  baseURL: url + "/api.php/"
  // transformRequest: [
  //   function(data, headers) {
  //     headers["Address"] = localStorage.getItem("userAddress");
  //     // if (!data) {
  //     //   return data;
  //     // }
  //     // if (data instanceof FormData) {
  //     //   return data;
  //     // } else {
  //     //   return qs.stringify(data);
  //     // }
  //   }
  // ]
});
api.interceptors.request.use(
  function(config) {
    // config.headers.Address = "0xce6dCEffBE6523d8f4aB3ABB1cf3bc5a484E1d6c";
    config.headers.Address = localStorage.getItem("userAddress");

    // 在发送请求之前做些什么
    return config;
  },
  function(error) {
    // 对请求错误做些什么
    return Promise.reject(error);
  }
);
export default {
  get(url, params) {
    return api
      .get(url, { params })
      .then(response => response.data)
      .catch(error => {
        console.error(error);
      });
  },

  post(url, data) {
    return api
      .post(url, data)
      .then(response => {
        if (response.data.code != 200) Toast(response.data.msg);
        return response.data;
      })
      .catch(error => {
        console.error(error);
      });
  },

  upload(url, data) {
    return api
      .post(url, data)
      .then(response => response.data)
      .catch(error => {
        console.error(error);
      });
  }
};



