import Web3 from "web3";
import { Toast } from "vant";
var web3 = new Web3(Web3.givenProvider);
export default {
  data() {
    return {
      zeroAddress: "0x0000000000000000000000000000000000000000"
    };
  },
  computed: {
    userAddress() {
      // return "0xce6dCEffBE6523d8f4aB3ABB1cf3bc5a484E1d6c";
      return (
        this.$store.state.userAddress || localStorage.getItem("userAddress")
      );
    }
  },
  methods: {
    fzdzhiAs() {
      Toast({
        message: this.$t("index.text153")
      });
    },
    toWei(n) {
      return Web3.utils.toWei(n.toString(), "ether");
    },
    fromWei(n) {
      return Web3.utils.fromWei(n.toString(), "ether");
    },
    isAddress(address) {
      return Web3.utils.isAddress(address.toLocaleLowerCase());
    },
    async getGasPrice() {
      return await web3.eth.getGasPrice();
    },
    // 授权方法
    async publicApprove(address, number) {
      let gasPrice = await this.getGasPrice();
      let gas = await this.core
        .usdtContract()
        .approve(address, this.toWei(number))
        .estimateGas();
      await this.core
        .usdtContract()
        .approve(address, this.toWei(number))
        .send({
          from: this.$store.state.userAddress,
          gas: gas + 5000,
          gasPrice: gasPrice
        });
    },

    setErrDialog(error) {
      this.$store.commit("setErrDialog", {
        errDialog: true,
        errMsg: error
      });
    }
  }
};



