// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from "vue";
import App from "./App";
import router from "./router";

Vue.config.productionTip = false;

//引入 Vant
import Vant from "vant";
import "vant/lib/index.css";
Vue.use(Vant);
//在main.js引入
import Web3 from "web3";
Vue.prototype.Web3 = Web3;
import { Dialog } from "vant";
Vue.use(Dialog);
// 导入api.js 方法
// 导入ElementUI
import ElementUI from "element-ui";
import "element-ui/lib/theme-chalk/index.css";
import "element-ui/lib/theme-chalk/display.css";
Vue.use(ElementUI);
import "@/assets/less/index.less";
import { Popup } from "vant";

Vue.use(Popup);
import commonMixin from "@/mixins/index.js";
Vue.mixin(commonMixin);
// 公用顶部
import Navbar from "../src/components/navbar.vue";
Vue.component("Navbar", Navbar);
// 错误弹窗
import ErrDialog from "../src/components/errDialog";
Vue.component("ErrDialog", ErrDialog);
ethereum.on("accountsChanged", () => {
  window.location.reload();
});
//监听链网络改变
ethereum.on("chainChanged", () => {
  window.location.reload();
});
// 复制
import VueClipboard from "vue-clipboard2";
Vue.use(VueClipboard);

import VueI18n from "vue-i18n";
import messages from "./lang/index.js";
Vue.use(VueI18n);
const i18n = new VueI18n({
  locale: localStorage.getItem("locale") || "CN",
  messages
});
Vue.prototype.lang = i18n;
// var $axios = require("./axios/index.js");
import $api from "./axios/index.js";
Vue.prototype.$api = $api;
import url from "../src/axios/url.js";
Vue.prototype.$url = url;
import store from "./store/index";
import core from "@/api/contractAddress.js";

Vue.prototype.core = core;
/* eslint-disable no-new */
new Vue({
  el: "#app",
  router,
  store,
  i18n,
  beforeCreate() {
    Vue.prototype.$bus = this;
  },
  components: { App },
  template: "<App/>"
});



