import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);
export default new Vuex.Store({
  state: {
    userAddress: "",
    userInfo: {},
    token: "",
    errDialog: false,
    errMsg: ""
  },
  mutations: {
    setUserAddress(state, payload) {
      state.userAddress = payload.address;
      state.token = payload.token;
    },
    setErrDialog(state, payload) {
      state.errDialog = payload.errDialog;
      state.errMsg = payload.errMsg;
    }
  },
  actions: {}
});



