<template>
  <div class="header">
    <div class="headerBox">
      <div class="jcc">
        <img class="logo2" src="@/assets/img/logo2.png" alt="" />
        <img
          class="logo"
          src="@/assets/img/logo.png"
          alt=""
          v-if="lang.locale == 'CN'"
        />
        <span class="LFCX" v-else>LFCX</span>
      </div>

      <div class="connectWallet jcc">
        <span>
          {{ userAddress | truncate }}
        </span>
      </div>

      <van-popover
        v-model="showPopover"
        trigger="click"
        :actions="actions"
        @select="onSelect"
        placement="bottom-end"
      >
        <template #reference>
          <img class="lang" src="@/assets/img/langIcon.png" alt="" />
        </template>
      </van-popover>
      <img
        class="navBarIcon"
        src="@/assets/img/navBarIcon.png"
        alt=""
        @click="showPopup = true"
      />

      <van-popup v-model="showPopup" position="right" class="showPopup">
        <div style="position: relative; height: 100%">
          <div class="jcc pic22">
            <img style="width: 90%" src="@/assets/img/pic22.png" alt="" />
          </div>

          <div class="popupHeader jcc">
            <div class="userAddressBox">
              <div class="userAddresTitle">{{ $t("index.text152") }}</div>
              <div class="userAddres">{{ userAddress }}</div>

              <div
                class="copyBox"
                v-clipboard:copy="userAddress"
                v-clipboard:success="fzdzhiAs"
              >
                <img src="@/assets/img/copy1.png" alt="" />
                <span class="copyText">{{ $t("index.text57") }}</span>
              </div>
            </div>
          </div>
          <div class="popupMain">
            <div class="content">
              <div class="navBarList">
                <div
                  class="navBarItem"
                  v-for="(item, index) in navBarList"
                  :key="index"
                  @click="skip(item.path)"
                >
                  <div
                    v-if="
                      index == 3
                        ? userInfo.identity == 1
                          ? true
                          : false
                        : true
                    "
                  >
                    <img
                      :src="require(`@/assets/img/navBar${index}.png`)"
                      alt=""
                    />

                    <span>{{ $t(item.lang) }}</span>

                    <van-icon name="arrow" />
                  </div>
                </div>

                <div
                  class="navBarItem"
                  v-if="userInfo.identity == 1"
                  @click="skip('/merchantCenter')"
                >
                  <div>
                    <img :src="require(`@/assets/img/navBar3.png`)" alt="" />

                    <span>{{ $t("navBar.text3") }}</span>

                    <van-icon name="arrow" />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </van-popup>
    </div>

    <div style="height: 5rem"></div>
  </div>
</template>

<script>
export default {
  filters: {
    truncate(value) {
      if (!value) return "";
      const firstSix = value.substring(0, 5);
      const lastFour = value.substring(value.length - 5);
      const middle = "...";
      return `${firstSix}${middle}${lastFour}`;
    }
  },
  data() {
    return {
      showPopup: false,
      showPopover: false,
      // 通过 actions 属性来定义菜单选项
      actions: [
        { text: "中文", lang: "CN" },
        { text: "English", lang: "EN" }
      ],
      navBarList: [
        {
          name: "首页",
          path: "/index",
          lang: "navBar.text"
        },
        {
          name: "个人中心",
          path: "/personalCenter",
          lang: "navBar.text1"
        },
        {
          name: "收货地址管理",
          path: "/addressManagement",
          lang: "navBar.text2"
        }

        // {
        //   name: "卖家中心",
        //   lang: "navBar.text3"
        // }
      ],
      userInfo: {}
    };
  },
  activated() {
    this.showPopup = false;
    // this.getUserInfo();
    // this.$bus.$on("openShop", () => {
    //   this.getUserInfo();
    // });
  },

  methods: {
    getUserInfo() {
      this.$api.get("user/info", {}).then(res => {
        this.userInfo = res.data;
      });
    },
    go() {
      window.history.back();
    },
    onSelect(v) {
      this.lang.locale = v.lang;
      localStorage.setItem("locale", v.lang);
    },
    skip(path) {
      if (path) {
        this.$router.push(path);
        this.showPopup = false;
      } else {
        window.open("https://pancakeswap.finance/swap?utm_source=tokenpocket");
      }
    }
  }
};
</script>
<style lang="less" scoped>
.header {
  .headerBox {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 10;
    height: 5rem;
    padding: 0.9rem 1.5rem;
    background: #d12c21;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .navBarIcon {
    width: 3rem;
    height: 3rem;
  }
  .logo2 {
    width: 3.9rem;
    height: 2.9rem;
    margin-right: 0.5rem;
  }
  .logo {
    width: 7.05rem;
    height: 2.25rem;
  }
  .LFCX {
    font-size: 2rem;
    font-weight: bold;
    color: #ffffff;
    letter-spacing: 0.45rem;
  }
  .lang {
    width: 2.5rem;
    height: 2.5rem;
  }
  .connectWallet {
    width: 10rem;
    height: 3rem;
    background: #ffffff;
    border-radius: 0.2rem;
    .connectWalletIcon {
      width: 1.7rem;
      height: 1.7rem;
    }
    span {
      color: #d12c21;
    }
  }

  .showPopup {
    width: 24.1rem;
    height: 100vh;
    background: #fffbf1;

    .pic22 {
      position: absolute;
      bottom: 2rem;
    }
    .popupHeader {
      padding: 3rem 2rem;
      .userAddressBox {
        width: 100%;
        height: 15rem;
        padding: 2rem;
        background: url("../assets/img/pic20.png") no-repeat;
        background-size: 100% 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        .userAddresTitle {
          color: #fff;
          font-weight: bold;
        }

        .userAddres {
          line-height: 2.5rem;
          color: #fff;
          font-size: 1.2rem;
        }

        .copyBox {
          display: flex;
          color: #fff;

          img {
            height: 1.6rem;
          }
          .copyText {
            color: #fff;
            margin-left: 0.5rem;
          }
        }
      }
    }

    .popupMain {
      padding: 0 0.5rem;
      .content {
        padding: 0 2rem;
        .navBarItem {
          margin-top: 3.2rem;

          div {
            display: flex;
            align-items: center;
            position: relative;
          }

          img {
            width: 2.2rem;
            height: 2.2rem;
          }
          span {
            margin-left: 1rem;
            font-weight: bold;
          }

          .van-icon {
            position: absolute;
            right: 0;
            color: #d0cfce;
            font-size: 1.8rem;
          }
        }
      }
    }
  }
}
</style>



