<template>
  <van-popup
    v-model="$store.state.errDialog"
    class="errDialog"
    :close-on-click-overlay="false"
  >
    <div class="content">
      <img src="../assets/img/close.png" class="close" alt="" @click="close" />
      <div class="title">{{ $t("index.text162") }}</div>
      <div class="errMsg mt2r">{{ $store.state.errMsg }}</div>
    </div></van-popup
  >
</template>

<script>
export default {
  methods: {
    close() {
      this.$store.commit("setErrDialog", {
        errDialog: false,
        errMsg: ""
      });
    }
  }
};
</script>

<style lang="less" scoped>
.errDialog {
  border-radius: 0.5rem;
  padding: 2rem;
  width: 90vw;
  background: #fcefd3;
  .content {
    position: relative;
  }
  .close {
    position: absolute;
    top: 0;
    right: 0;
    width: 2rem;
    height: 2rem;
  }
  .title {
    text-align: center;
    font-size: 1.6rem;
    font-weight: bold;
  }
  .errMsg {
    text-align: center;
    line-height: 2.5rem;
    color: #d12c21;
  }
  .confirm {
    height: 4rem;
    background: linear-gradient(134deg, #d12c21 0%, #f5c96d 100%);
    box-shadow: 2px 5px 20px 1px rgba(255, 16, 0, 0.28);
    border-radius: 0.8rem;
    font-size: 1.6rem;
    color: #fff;
    font-weight: bold;
  }
}
</style>



