<script>
export default {
  name: "App"
};
</script>

<style lang="less">
div {
  box-sizing: border-box;
  font-size: 1.4rem;
  word-break: break-all;
  color: #000;
}

span {
  color: #000;
  font-size: 1.4rem;
}
#app {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background: #fff;
}
.allowNftId {
  .van-dialog__header {
    color: #000000 !important;
  }
  .van-dialog__confirm {
    color: #fff !important;
  }
}

html {
  font-size: 2.6666666665vw !important;
}

body {
  margin: 0px;
  background: #000000;
}
</style>



