import Web3 from "web3";
import coreAbi from "@/api/abi/Core.json";
import tokenAbi from "@/api/abi/token.json";

var web3 = new Web3(Web3.givenProvider);
import store from "@/store/index.js";

// op测试网
// const coreAddress = "0x381AAEFf136323a6297c12caA17B61b4048853E1";
// const hlAddress = "0xe3b7bfeac8b6cf0a84ec4be3df44eadf6682bc38";
// const usdtAddress = "0x0bc7050aE079cD654F2f972B0B7A305Cb4Dd9A67";
// const hfAddress = "0xf91b04F0D8f7955cadbb3c486b3530c0A02d4A37";
// op主网
const coreAddress = "0x6D2777709e528022FBf9Bd9A8B762c6414af78eB";
const hlAddress = "0x9c1E46254F9eDDA2D3139dc7255240E1Ed0c8754";
const usdtAddress = "0x9e5AAC1Ba1a2e6aEd6b32689DFcF62A509Ca96f3";
const hfAddress = "0x2D8DC07CAF9B14f55de21dA09a2622165A7eef2A";
 



//  核心合约
const coreContract = function() {
  const contract = new web3.eth.Contract(coreAbi, coreAddress, {
    from: store.state.userAddress
  });

  return contract.methods;
};
//  核心合约

//  火龙代币合约
const hlContract = function() {
  const contract = new web3.eth.Contract(tokenAbi, hlAddress, {
    from: store.state.userAddress
  });
  return contract.methods;
};

//  USDT代币合约
const usdtContract = function() {
  const contract = new web3.eth.Contract(tokenAbi, usdtAddress, {
    from: store.state.userAddress
  });
  return contract.methods;
};

//  积分代币合约
const hfContract = function() {
  const contract = new web3.eth.Contract(tokenAbi, hfAddress, {
    from: store.state.userAddress
  });
  return contract.methods;
};

export default {
  coreAddress: coreAddress,
  hlAddress: hlAddress,
  hfAddress: hfAddress,
  usdtAddress: usdtAddress,
  coreContract: coreContract,
  hlContract: hlContract,
  hfContract: hfContract,
  usdtContract: usdtContract,
  web3: web3
};



